import React, { useState, Fragment } from "react";
import Header from "./sections/Header";
import Introduction from "./sections/Introduction";
import SustanabilitySurvey from "./sections/SustanabilitySurvey";
import ThankYou from "./sections/ThankYou";

function App() {

const [answersSent, setAnswersSent] = useState(false)
const submitHandler = () => {
	setAnswersSent(true);
};

	return (
		<Fragment>
			<Header />
			{!answersSent && <Introduction />}
			{!answersSent && <SustanabilitySurvey onClick={submitHandler} />}
			{answersSent && <ThankYou />}
		</Fragment>
	);
}

export default App;
