import ContactForm from "./ContactForm";
import React, { Fragment, useState } from "react";
import FormWrapper from "../components/FormWrapper";
import SurveyForm from "./SurveyForm";
<SurveyForm />;

const SustanabilitySurvey = (props) => {
const [fillingSurvey, setFillingSurvey] = useState(false);

const displayHandler = () => {
		
		setFillingSurvey(true)
	}

const submitHandler = (event) => {
		event.preventDefault();
		props.onClick()

	  };
	return (
		<Fragment>
			<FormWrapper>
				<form
					className="form-horizontal"
					name="survey-form"
					action="/survey.php"
					id="survey-form"
					method="POST"
					onSubmit={submitHandler} 
				>
					<ContactForm onClick={displayHandler} />
					{fillingSurvey && <SurveyForm />}
				</form>
			</FormWrapper>
		</Fragment>
	);
};

export default SustanabilitySurvey;
