import React from 'react';

const InputTwo = (props) => {

return (
    <div className="question">
    
    <input className={props.className} type={props.type} id={props.id} name={props.id}  /><label htmlFor={props.id}>{props.label}
    </label>
    <br />
    </div>
);
};
export default InputTwo;