import React from 'react';

const Input = (props) => {

return (
    <div className="question">
    
    <input className={props.className} type={props.type} id={props.id} name={props.id} value={props.value} placeholder={props.placeholder} required /><label htmlFor={props.id}>{props.label}
    </label>
    <br />
    </div>
);
};
export default Input;