import "./Introduction.scss";

const Introduction = () => {
	return (
		<div className="container" id="home">
			<div className="row">
				<div className="col-sm-12 col-md-10 col-lg-8 text-center my-4 mx-auto">
					<h1 className="surveytitle">Quick Sustainability Survey</h1>

					<p id="description">
						Today we live in time when we're surrounded by environmentalist
						topics. What has been just about 10 years ago interest just for a
						small group of "green fanatics" is now a widely discussed issue.
						Everyday more and more products labeled as "eco-friendly", "green",
						"good for the planet", "made by nature" "zero-waste", "reusable" are
						ready to jump into our shopping carts. Most passionate talks swirl
						around the term "climate change". We also like to refer to our
						society as "modern" or "advanced" so we like to assume that the
						things that are supposed to work are certainly taken care of
						somehow. But are they really? Or is it all just another clever trick
						played on our conscience? That's what we'd love to find out so we
						put together this quick survey. Here's our short list of questions
						that can help us map what relationship we as people have to planet
						Earth. All data will be processed anonymously and no privacy rights
						will be violated. Please answer them honestly, no one's here to
						judge you.
					</p>
					<br />
					<a href="#contactform">
						<button id="button-one" class="btn btn-md btn-warning">
							Let's begin!
						</button>
					</a>
				</div>
			</div>
		</div>
	);
};
export default Introduction;
