import "./SurveyForm.scss";
import Button from "../components/Button";
import React, { Fragment } from "react";
import Input from "../components/Inputs/Input.js";
import DropdownInput from "../components/Inputs/DropdownInput";
import Wrappy from "../components/Wrappy";
import Wrap from "../components/Wrap";
import InputTwo from "../components/Inputs/InputTwo";


const SurveyForm = () => {

let optionValsOne = [{value: "everytime", answer: "Everytime"},{value:"several_times_a_month", answer: "Several times a month"}, {value: "once", answer: "Once a month"}, {value: "never", answer: "Never"}];

const selectOptionsOne = optionValsOne.map((item) => ( <option value={item.value}>{item.answer}</option>));


let optionValsTwo = [{value: "china", answer: "China"}, {value: "thailand", answer: "Thailand"}, {value: "indonesia", answer: "Indonesia"}]
const selectOptionsTwo = optionValsTwo.map((item) => ( <option value={item.value}>{item.answer}</option>));

let optionValsThree = [{value: "several", answer: "Several times a month"}, {value: "once", answer: "Once a month or less"}, {value: "year", answer: "Once a year or less"},{value: "never", answer: "Never"}]
const selectOptionsThree = optionValsThree.map((item) => ( <option value={item.value}>{item.answer}</option>));


let optionValsFour = [{value: "several_times_a_day", answer: "Several times a day"}, {value: "several_times_a_week", answer: "Several times a week"}, {value: "several_times_a_month", answer: "Several times a month"}, {value: "once", answer: "Once a month or less"}, {value: "year", answer: "Once a year or less"},{value: "never", answer: "Never"}]
const selectOptionsFour = optionValsFour.map((item) => ( <option value={item.value}>{item.answer}</option>));

let optionValsFive = [{value: "secondhand", answer: "Secondhand"}, {value: "organiccotton", answer: "Organic cotton"}, {value: "cotton", answer: "Cotton"}, {value: "polymer", answer: "Polymer fibers"}, {value: "luxurious", answer: "Luxurious brands"}]
const selectOptionsFive = optionValsFive.map((item) => ( <option value={item.value}>{item.answer}</option>));

let optionValsSix = [{value:"popular", answer:"Popular brands"}, {value:"ecofriendly", answer:"Eco-friendly brands"}, {value:"reusable", answer:"Organic brands in reusable packaging"}]
const selectOptionsSix = optionValsSix.map((item) => ( <option value={item.value}>{item.answer}</option>));


	return (
	<Fragment>
		<div id="survey_start">

 <Wrap>
 <legend htmlFor="textarea1" > What comes first to your mind when you hear the word "sustainability"?</legend>

  <textarea className="form-control" rows="5" id="textarea1"
				minLength="50"
				maxLength="500"
				required></textarea>
</Wrap>
 
 <legend>Do you often drink alcohol?</legend>
 <Wrappy>
  <Input  className="form-check-input" id="drink" type="radio" value="yes" label="Yes" />
 </Wrappy>
 <Wrappy>
 <Input className="form-check-input" id="drink" type="radio" value="no" label="No" />
</Wrappy>
 <legend>Do you often smoke?</legend>
 <Wrappy>
 <Input className="form-check-input" id="smoke" type="radio" value="yes" label="Yes" />
 </Wrappy>
 <Wrappy>
 <Input className="form-check-input" id="smoke" type="radio" value="no" label="No" />
</Wrappy>
<legend>Which animal do you keep as a pet?</legend>
<Wrappy>
 <InputTwo className="form-check-input" type="checkbox" id="checkanimal" value="pig" label="Pig"/>
 </Wrappy>
 <Wrappy>
 <InputTwo className="form-check-input" type="checkbox" id="checkanimal" value="chicken" label="Chicken"/>
 </Wrappy>
 <Wrappy>
 <InputTwo className="form-check-input" type="checkbox" id="checkanimal" value="zebra" label="Zebra"/>
 </Wrappy>
 <Wrappy>
     <InputTwo className="form-check-input" type="checkbox" id="checkanimal" value="dog" label="Dog"/>
     </Wrappy>
     <Wrappy>
 <InputTwo className="form-check-input" type="checkbox" id="checkanimal" value="cat" label="Cat"/>
 </Wrappy>
<legend>Do you drive a car?</legend>
<Wrappy>
<Input className="form-check-input" id="carbtn" name="car"  type="radio" value="yes" label="Yes" />
</Wrappy>
<Wrappy>
<Input className="form-check-input" id="carbtn" name="car"  type="radio" value="no" label="No" />
</Wrappy>
<legend >How often do you buy unwrapped food?</legend>
        <DropdownInput  id="food"
        options={selectOptionsOne} />
       
        <legend for="phoneorigin">Where was your phone made?</legend>
        <DropdownInput  id="phoneorigin"
        options={selectOptionsTwo}
          />
    
        
        <legend>Which of these places are highly radioactive??</legend>
    <br />
    <Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="chernobyl" label="Chernobyl" />
        </Wrappy>
        <Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="fukushima" label="Fukushima" />
       </Wrappy>
        <Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="siberia" label="Siberia" />
</Wrappy>
<Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="pacific" label="Islands in the Pacific Ocean" />
        </Wrappy>
        <Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="mediterraneansea" label="Mediterranean Sea" />
        </Wrappy>
        <Wrappy>
        <InputTwo className="form-check-input" type="checkbox" id="checkrad" value="nevada" label="Nevada" />
        </Wrappy>

<legend>If you wear a mask is it because of coronavirus or air pollution?</legend>
<Wrappy>
<InputTwo className="form-check-input" type="checkbox" id="mask"  label="Air pollution" />
</Wrappy>
<Wrappy>
<InputTwo className="form-check-input" type="checkbox" id="mask"  label="Coronavirus" />
</Wrappy>

<legend for="fly">How often do you fly?</legend>

        <DropdownInput id="fly" 
        options={selectOptionsThree}/>
       
    <legend for="animals">How often do you eat animals?</legend>
   
    <DropdownInput id="animals" options={selectOptionsFour}/>
      
    <legend for="clothes">What type of clothes do you mostly like to wear?</legend>
    <DropdownInput id="clothes" options={selectOptionsFive}/>


    <legend for="cleaning">What type of household cleaning products do you use?</legend>
   
    <DropdownInput id="cleaning" options={selectOptionsSix}/>
           
  
    <legend>Where do you most often get the book you read?</legend>
    <Wrappy>
<InputTwo className="form-check-input" type="radio" id="books"  value="bookshop" label="Bookshop" />
</Wrappy>
<Wrappy>
<InputTwo className="form-check-input" type="radio" id="books"  value="library" label="Library" />
</Wrappy>
<Wrappy>
<InputTwo className="form-check-input" type="radio" id="books"  value="online" label="Online"/>
</Wrappy>

<legend>How many trees have you planted in your whole life?</legend>
<Wrappy> 
<InputTwo className="form-check-input" type="radio" id="trees" value="zero" label="0" />
</Wrappy>
<Wrappy>
<InputTwo className="form-check-input" type="radio" id="trees" value="ten" label="1 - 10" />
</Wrappy>
<Wrappy>
<InputTwo className="form-check-input" type="radio" id="trees" value="hundred" label="10 - 100" />
</Wrappy>


<Wrap>
 <legend htmlFor="textarea2">Did you know that 2/3 of all wild animals that lived on the planet are now gone? What do you think about it?</legend>

  <textarea className="form-control" rows="5" id="textarea2"
				minLength="50"
				maxLength="500"
				required></textarea>
</Wrap>


 <Button type="submit" label="Submit my answers!"></Button>
 </div>
</Fragment>
    );
    };
export default SurveyForm;
