import React from 'react';

const InputOne = (props) => {

return (
    <div className="question">
    
    <label htmlFor={props.id}>{props.label}
    </label><input className={props.className} type={props.type} id={props.id} name={props.id} value={props.value} placeholder={props.placeholder} required />
    <br />
    </div>
);
};
export default InputOne;