import "./ContactForm.scss";
import Wrap from "../components/Wrap";
import Wrappy from "../components/Wrappy";
import React from "react";
import InputOne from "../components/Inputs/InputOne";
import InputTwo from "../components/Inputs/InputTwo";

const ContactForm = (props) => {

	function onlyNumberKey(evt) {
		// Only ASCII character in that range allowed
		var ASCIICode = evt.which ? evt.which : evt.keyCode;
		if (ASCIICode > 31 && (ASCIICode < 48 || ASCIICode > 57)) return false;
		return true;
	}

	const arrRadio = [
		{
			id: "place1",
			value: "asia",
			answer: "Asia",
		},
		{ id: "place2", value: "northamerica", answer: "North America" },
		{
			id: "place3",
			value: "southamerica",
			answer: "South America",
		},
		{
			id: "place4",
			value: "europe",
			answer: "Europe",
		},
		{
			id: "place5",
			value: "africa",
			answer: "Africa",
		},
		{
			id: "place6",
			value: "australia",
			answer: "Australia",
		}
	];

	const radioCountry = arrRadio.map((item) => (
		<Wrappy>
			<InputTwo
				type="radio"
				id={item.id}
				className="form-check-input"
				value={item.value}
				label={item.answer}
			/>
			<br />
		</Wrappy>
	));

	const displayHandler = (event) => {
		props.onClick()
	}
	return (
		<section id="contactform">
			<fieldset>
				<legend className="legend">
					First, tell us something about yourself
				</legend>
				<ul>
					<li>
					<Wrap>
					<InputOne
							label="Name:"
							type="text"
							className="form-control"
							id="name"
							placeholder="e. g. Annie Black"
						/>
					</Wrap></li>
					<li><Wrap>
					<InputOne
							label="Email:"
							type="email"
							className="form-control"
							name="useremail"
							id="email"
							placeholder="lovelyperson@email.com"
				
						/>
					</Wrap></li>
					<li><Wrap>
					<InputOne label="Phone Number:"
							type="tel"
							className="form-control"
							id="usernumber"
							placeholder="098765432"
							minlength="9"
							maxlength="13"
							onInput={onlyNumberKey}
							required
						/>
					</Wrap></li>
					<li><Wrap>
					<InputOne	label="Your Age:"
							type="number"
							className="form-control"
							id="yourage"
						
						/>
					</Wrap></li>
					<li><Wrap>

    <div class="form-group">
    <label className="control-label" htmlFor="dropdown">Your Education:
						</label>
						<select id="dropdown" className="form-select"required> 
						<option disabled selected value>Select</option>
							<option value="no">No school</option>
							<option value="elementary">Elementary School</option>
							<option value="highschool">High School</option>
							<option value="university">University Degree</option>
							<option value="professional">Professional degree</option>
						</select>
						</div>
					</Wrap></li>
					<li><label>Where do you live?</label>
						<br />
					{radioCountry}
					</li>
					
				</ul>
			</fieldset>
			<a href="#survey_start">
			<button type="button" onClick={displayHandler} className="btn btn-md btn-warning">
				I'm ready!
			</button></a>
		</section>
	);
};
export default ContactForm;
