import "./Header.scss";
import headerImage_sm from "../img/Landscape_sm.jpg";
import headerImage_md from "../img/Landscape_md.jpg";
import headerImage_lg from "../img/Landscape_lg.jpg";

const Header = () => {
	return (
		<div className="container-fluid text-right">
		
			<picture>
  <source media="(min-width: 480px)"
 srcset={`${headerImage_sm} 400w,
 ${headerImage_md} 800w,
 ${headerImage_lg} 1200w`}
sizes="100vw"/>
  <img src={headerImage_sm} class="img-fluid" alt="Mountains"/>
  </picture>
			<h1 className="maintitle" id="title">
				PLANET EARTH IS OUR ONLY HOME
			</h1>
			<h2 className="subtitle">What do we do to protect it?</h2>
			<a id="entry-scroller" href="#home">
				<i className="fas fa-chevron-down"></i>
			</a>
		</div>
	);
};
export default Header;
